import './custom-css'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignHorizontal from './images/horizontal.png'
import ImageDesignHorizontal2 from './images/horizontal-2.png'
import ImageDesignHorizontal3 from './images/horizontal-3.png'
import ImageDesignSplitCentered from './images/split-centered.png'
import { isPro } from 'stackable'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro ) {
	addFilter( 'stackable.cta.edit.designs', 'stackable/cta', ret => {
		return [ ...ret,
			{
				label: __( 'Horizontal' ), value: 'horizontal', image: ImageDesignHorizontal,
			},
			{
				label: __( 'Horizontal 2' ), value: 'horizontal-2', image: ImageDesignHorizontal2,
			},
			{
				label: __( 'Horizontal 3' ), value: 'horizontal-3', image: ImageDesignHorizontal3,
			},
			{
				label: __( 'Split Centered' ), value: 'split-centered', image: ImageDesignSplitCentered,
			},
		]
	} )

	addFilter( 'stackable.cta.edit.inspector.after', 'stackable/cta', ( output, design, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	} )
}

addFilter( 'stackable.cta.mainclasses', 'stackable/cta', ( classes, design, props ) => {
	const { hoverEffect = '' } = props.attributes
	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )
