import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-card'
const defaultCSS = `/* Card container */
.ugb-card {
\t
}

/* Card image */
.ugb-card .ugb-card__image-container {
\t
}

/* Card title */
.ugb-card .ugb-card__title {
\t
}

/* Card tagline */
.ugb-card .ugb-card__tagline {
\t
}

/* Card description */
.ugb-card .ugb-card__description {
\t
}

/* Card button */
.ugb-card .ugb-button {
\t
}

/* Card button text */
.ugb-card .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.card.edit.inspector.after', 'stackable/card', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.card.edit.output.before', 'stackable/card', outputStyle )
addFilter( 'stackable.card.save.output.before', 'stackable/card', outputStyle )

addFilter( 'stackable.card.mainclasses', 'stackable/card', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
