import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import classnames from 'classnames'
import ImageDesignAbstract from './images/abstract.png'
import ImageDesignBoxed from './images/boxed.png'
import ImageDesignSide from './images/side.png'
import { omit } from 'lodash'
import { PanelBackgroundSettings } from '@stackable/components'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.count-up.edit.designs', 'stackable/count-up', ret => {
		return [ ...ret,
			{
				label: __( 'Side' ), value: 'side', image: ImageDesignSide,
			},
			{
				label: __( 'Abstract' ), value: 'abstract', image: ImageDesignAbstract,
			},
			{
				label: __( 'Boxed' ), value: 'boxed', image: ImageDesignBoxed,
			},
		]
	} )
}

addFilter( 'stackable.count-up.edit.output', 'stackable/count-up', ( output, comps, i, props ) => {
	const {
		design,
		borderRadius,
		shadow,
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
	} = props.attributes
	if ( design === 'side' || design === 'abstract' ) {
		return (
			<div className="ugb-countup__item" key={ i }>
				{ comps.countComp }
				<div className="ugb-countup__side">
					{ comps.titleComp }
					{ comps.descriptionComp }
				</div>
			</div>
		)
	}

	if ( design === 'boxed' ) {
		const itemClasses = classnames( [
			'ugb-countup__item',
			`ugb--shadow-${ shadow }`,
		], {
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		} )
		const itemStyles = {
			borderRadius,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColor || backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		return (
			<div className={ itemClasses } style={ itemStyles } key={ i }>
				{ comps.titleComp }
				{ comps.countComp }
				{ comps.descriptionComp }
			</div>
		)
	}

	return output
} )

