import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignHalf from './images/half.png'
import ImageDesignHorizontal from './images/horizontal.png'
import ImageDesignOverlay from './images/overlay.png'
import { ToggleControl } from '@wordpress/components'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.team-member.edit.designs', 'stackable/team-member', ret => {
		return [ ...ret,
			{
				label: __( 'Horizontal' ), value: 'horizontal', image: ImageDesignHorizontal,
			},
			{
				label: __( 'Overlay' ), value: 'overlay', image: ImageDesignOverlay,
			},
			{
				label: __( 'Half' ), value: 'half', image: ImageDesignHalf,
			},
		]
	} )

	addFilter( 'stackable.team-member.edit.general.before', 'stackable/team-member', ( ret, design, props ) => {
		const { setAttributes } = props
		const { colorOnHover } = props.attributes
		return (
			<ToggleControl
				label={ __( 'Color on Hover' ) }
				checked={ colorOnHover }
				onChange={ colorOnHover => setAttributes( { colorOnHover } ) }
			/>
		)
	} )
}

addFilter( 'stackable.team-member.edit.output', 'stackable/team-member', ( output, design, comps ) => {
	if ( [ 'overlay', 'horizontal', 'half' ].includes( design ) ) {
		return (
			<Fragment>
				{ comps.imageComp }
				<div className="ugb-team-member__content">
					{ comps.nameComp }
					{ comps.positionComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	}
	return output
} )

addFilter( 'stackable.team-member.save.output', 'stackable/team-member', ( output, design, comps, i, props ) => {
	const mediaURL = props.attributes[ `mediaURL${ i }` ]
	const imageStyles = {
		backgroundImage: mediaURL ? `url(${ mediaURL })` : undefined,
	}

	if ( design === 'overlay' || design === 'half' ) {
		return (
			<Fragment>
				<div className="ugb-team-member__image" style={ imageStyles } data-src={ mediaURL } />
				<div className="ugb-team-member__content">
					{ comps.nameComp }
					{ comps.positionComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	} else if ( design === 'horizontal' ) {
		return (
			<Fragment>
				{ comps.imageComp }
				<div className="ugb-team-member__content">
					{ comps.nameComp }
					{ comps.positionComp }
					{ comps.descriptionComp }
				</div>
			</Fragment>
		)
	}
	return output
} )

addFilter( 'stackable.team-member.edit.show', 'stackable/team-member', ( show, design ) => {
	if ( design === 'overlay' || design === 'half' ) {
		show.imageShape = false
	}
	return show
} )

addFilter( 'stackable.team-member.mainclasses', 'stackable/team-member', ( classes, design, props ) => {
	const {
		colorOnHover = false,
	} = props.attributes
	return {
		...classes,
		'ugb-team-member--color-on-hover': colorOnHover,
	}
} )

addFilter( 'stackable.team-member.itemclasses', 'stackable/team-member', ( classes, design, i, props ) => {
	if ( [ 'overlay', 'horizontal', 'half' ].includes( design ) ) {
		const {
			shadow = 3,
		} = props
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )

addFilter( 'stackable.team-member.itemstyles', 'stackable/team-member', ( styles, design, i, props ) => {
	if ( [ 'overlay', 'horizontal', 'half' ].includes( design ) ) {
		const {
			borderRadius = 12,
		} = props.attributes
		return {
			...styles,
			item: {
				...styles.item,
				borderRadius: borderRadius ? borderRadius : undefined,
			},
		}
	}
	return styles
} )
