import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignBackground from './images/background.png'
import ImageDesignFaded from './images/faded.png'
import ImageDesignHeading from './images/heading.png'
import ImageDesignHeading2 from './images/heading2.png'
import { omit } from 'lodash'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.number-box.edit.designs', 'stackable/number-box', ret => {
		return [ ...ret,
			{
				label: __( 'Background' ), value: 'background', image: ImageDesignBackground,
			},
			{
				label: __( 'Heading' ), value: 'heading', image: ImageDesignHeading,
			},
			{
				label: __( 'Heading 2' ), value: 'heading2', image: ImageDesignHeading2,
			},
			{
				label: __( 'Faded' ), value: 'faded', image: ImageDesignFaded,
			},
		]
	} )

	addFilter( 'stackable.number-box.edit.show', 'stackable/number-box', ( show, design ) => {
		if ( [ 'background', 'heading', 'heading2' ].includes( design ) ) {
			return {
				...show,
				numberBGColor: false,
			}
		}
		if ( design === 'faded' ) {
			return {
				...show,
				backgroundColor: false,
				borderRadius: false,
				shadow: false,
				numberColor: false,
				numberBGColor: false,
			}
		}
		return show
	} )
}

addFilter( 'stackable.number-box.styles', 'stackable/number-box', ( styles, design, props ) => {
	const { numberColor } = props.attributes
	if ( [ 'background', 'heading', 'heading2' ].includes( design ) ) {
		styles.number = omit( {
			...styles.number,
			color: numberColor ? numberColor : undefined,
		}, [ 'backgroundColor' ] )
	}
	if ( design === 'faded' ) {
		styles.number = omit( styles.number, [ 'backgroundColor', 'color' ] )
		styles.box = omit( styles.box, [ 'borderRadius', 'backgroundColor' ] )
	}
	return styles
} )

addFilter( 'stackable.number-box.boxclasses', 'stackable/number-box', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'faded' ) {
		return omit( classes, [ `ugb--shadow-${ shadow }` ] )
	}
	return classes
} )
