import './custom-css'
import { addFilter } from '@wordpress/hooks'
import classnames from 'classnames'

addFilter( 'stackable.blog-posts.edit.output', 'stackable/blog-posts', ( ret, design, props ) => {
	const {
		i, category, image, featuredImage, author, date, comments, title, excerpt, readMore,
	} = props
	const {
		displayDate,
		displayAuthor,
		displayComments,
		borderRadius,
		shadow,
	} = props.attributes

	if ( design === 'portfolio' || design === 'portfolio2' ) {
		const itemClasses = classnames( [
			'ugb-blog-posts__item',
			`ugb--shadow-${ shadow }`,
		] )

		const itemStyle = {
			backgroundImage: image ? `url(${ image })` : undefined,
			borderRadius: borderRadius ? borderRadius : undefined,
		}

		return (
			<article key={ i } className={ itemClasses } style={ itemStyle }>
				<div className="ugb-blog-posts__side">
					{ category }
					{ title }
					{ excerpt }
					{ readMore }
					{ ( displayDate || displayAuthor || displayComments ) &&
						<aside className="entry-meta ugb-blog-posts__meta">
							{ author }
							{ displayAuthor && ( displayDate || displayComments ) &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ date }
							{ displayDate && displayComments &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ comments }
						</aside>
					}
				</div>
			</article>
		)
	}

	if ( design === 'vertical-card' || design === 'horizontal-card' ) {
		const itemClasses = classnames( [
			'ugb-blog-posts__item',
			`ugb--shadow-${ shadow }`,
		] )

		const itemStyle = {
			borderRadius: borderRadius ? borderRadius : undefined,
		}

		return (
			<article key={ i } className={ itemClasses } style={ itemStyle }>
				{ featuredImage }
				<div className="ugb-blog-posts__side">
					{ category }
					{ title }
					{ excerpt }
					{ readMore }
					{ ( displayDate || displayAuthor || displayComments ) &&
						<aside className="entry-meta ugb-blog-posts__meta">
							{ author }
							{ displayAuthor && ( displayDate || displayComments ) &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ date }
							{ displayDate && displayComments &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ comments }
						</aside>
					}
				</div>
			</article>
		)
	}

	if ( design === 'vertical-card2' ) {
		const itemClasses = classnames( [
			'ugb-blog-posts__item',
			`ugb--shadow-${ shadow }`,
		] )

		const itemStyle = {
			borderRadius: borderRadius ? borderRadius : undefined,
		}

		return (
			<article key={ i } className={ itemClasses } style={ itemStyle }>
				<div className="ugb-blog-posts__side">
					{ category }
					{ title }
					{ excerpt }
					{ readMore }
				</div>
				{ featuredImage }
				{ ( displayDate || displayAuthor || displayComments ) &&
					<aside className="entry-meta ugb-blog-posts__meta">
						{ author }
						{ displayAuthor && ( displayDate || displayComments ) &&
							<span className="ugb-blog-posts__sep">&middot;</span>
						}
						{ date }
						{ displayDate && displayComments &&
							<span className="ugb-blog-posts__sep">&middot;</span>
						}
						{ comments }
					</aside>
				}
			</article>
		)
	}

	if ( design === 'image-card' ) {
		const itemClasses = classnames( [
			'ugb-blog-posts__item',
			`ugb--shadow-${ shadow }`,
		] )

		const itemStyle = {
			borderRadius: borderRadius ? borderRadius : undefined,
		}

		const headerStyle = {
			backgroundImage: image ? `url(${ image })` : undefined,
		}

		return (
			<article key={ i } className={ itemClasses } style={ itemStyle }>
				<div className="ugb-blog-posts__header" style={ headerStyle }>
					{ ( displayDate || displayAuthor || displayComments ) &&
						<aside className="entry-meta ugb-blog-posts__meta">
							{ author }
							{ displayAuthor && ( displayDate || displayComments ) &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ date }
							{ displayDate && displayComments &&
								<span className="ugb-blog-posts__sep">&middot;</span>
							}
							{ comments }
						</aside>
					}
					{ title }
				</div>
				<div className="ugb-blog-posts__side">
					{ excerpt }
					{ readMore }
					{ category }
				</div>
			</article>
		)
	}

	return ret
} )

addFilter( 'stackable.blog-posts.edit.show', 'stackable/blog-posts', ( show, design ) => {
	if ( [ 'portfolio', 'portfolio2', 'image-card' ].includes( design ) ) {
		return {
			...show,
			featuredImage: false,
		}
	}
	return show
} )
