import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-pricing-box'
const defaultCSS = `/* Pricing box container */
.ugb-pricing-box {
\t
}

/* Pricing box item */
.ugb-pricing-box .ugb-pricing-box__item {
\t
}

/* Pricing box image */
.ugb-pricing-box .ugb-pricing-box__image {
\t
}

/* Pricing box title */
.ugb-pricing-box .ugb-pricing-box__title {
\t
}

/* Pricing box price wrapper */
.ugb-pricing-box .ugb-pricing-box__price-line {
\t
}

/* Pricing box price prefix (currency) */
.ugb-pricing-box .ugb-pricing-box__price-prefix {
\t
}

/* Pricing box price (amount) */
.ugb-pricing-box .ugb-pricing-box__price {
\t
}

/* Pricing box price suffix (remainder) */
.ugb-pricing-box .ugb-pricing-box__price-suffix {
\t
}

/* Pricing box sub price description */
.ugb-pricing-box .ugb-pricing-box__subprice {
\t
}

/* Pricing box description text */
.ugb-pricing-box .ugb-pricing-box__description {
\t
}

/* Pricing box button */
.ugb-pricing-box .ugb-button {
\t
}

/* Pricing box button text */
.ugb-pricing-box .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.pricing-box.edit.inspector.after', 'stackable/pricing-box', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.pricing-box.edit.output.before', 'stackable/pricing-box', outputStyle )
addFilter( 'stackable.pricing-box.save.output.before', 'stackable/pricing-box', outputStyle )

addFilter( 'stackable.pricing-box.mainclasses', 'stackable/pricing-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
