import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-separator'
const defaultCSS = `/* Separator container */
.ugb-separator {
\t
}

/* Separator SVG wrapper */
.ugb-separator .ugb-separator__svg-wrapper {
\t
}

/* SVG Layer 1 (top layer) */
.ugb-separator .ugb-separator__layer-1 {
\t
}

/* SVG Layer 1 Shadow */
.ugb-separator .ugb-separator__shadow {
\t
}

/* SVG Layer 2 */
.ugb-separator .ugb-separator__layer-2 {
\t
}

/* SVG Layer 3 */
.ugb-separator .ugb-separator__layer-3 {
\t
}

/* Top padding */
.ugb-separator .ugb-separator__top-pad {
\t
}

/* Bottom padding */
.ugb-separator .ugb-separator__bottom-pad {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.separator.edit.inspector.after', 'stackable/separator', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.separator.edit.output.before', 'stackable/separator', outputStyle )
addFilter( 'stackable.separator.save.output.before', 'stackable/separator', outputStyle )

addFilter( 'stackable.separator.mainclasses', 'stackable/separator', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
