import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignColored from './images/colored.png'
import ImageDesignColoredHover from './images/colored-hover.png'
import ImageDesignLineColored from './images/line-colored.png'
import ImageDesignLineColoredHover from './images/line-colored-hover.png'
import isDarkColor from 'is-dark-color'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.accordion.edit.designs', 'stackable/accordion', ret => {
		return [ ...ret,
			{
				label: __( 'Lined to Colored' ), value: 'line-colored', image: ImageDesignLineColored, hoverImage: ImageDesignLineColoredHover,
			},
			{
				label: __( 'Colored' ), value: 'colored', image: ImageDesignColored, hoverImage: ImageDesignColoredHover,
			},
		]
	} )
}

addFilter( 'stackable.accordion.mainclasses', 'stackable/accordion', ( classes, design, props ) => {
	const { shadow, headingBackgroundColor } = props.attributes
	if ( design === 'line-colored' || design === 'colored' ) {
		return {
			...classes,
			'ugb-accordion--is-dark': headingBackgroundColor ? isDarkColor( headingBackgroundColor ) : false,
			[ `ugb--shadow-${ shadow }` ]: shadow !== 3,
		}
	}
	return classes
} )

addFilter( 'stackable.accordion.styles', 'stackable/accordion', ( styles, design, props ) => {
	const { borderRadius, headingBackgroundColor } = props.attributes

	if ( design === 'line-colored' || design === 'colored' ) {
		return {
			...styles,
			heading: {},
			main: {
				...styles.main,
				backgroundColor: headingBackgroundColor ? headingBackgroundColor : undefined,
				'--ugb-arrow-fill': headingBackgroundColor ? headingBackgroundColor : undefined,
				borderRadius: borderRadius !== 12 ? borderRadius : undefined,
			},
		}
	}
	return styles
} )

addFilter( 'stackable.accordion.headingclasses', 'stackable/accordion', ( classes, design ) => {
	if ( design === 'line-colored' || design === 'colored' ) {
		return {}
	}
	return classes
} )
