import { PanelBody, SelectControl, ToggleControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'

function PanelEffectsSettings( props ) {
	const {
		hoverEffect,
		grayscaleEffect,
		onChangeHoverEffect,
		onChangeGrayscaleEffect,
	} = props

	return (
		<PanelBody
			initialOpen={ false }
			title={ __( 'Effects' ) }
			{ ...props }
		>
			{ onChangeHoverEffect &&
				<SelectControl
					label={ __( 'Hover Effect' ) }
					value={ hoverEffect }
					options={ [
						{ value: '', label: __( 'None' ) },
						{ value: 'shadow', label: __( 'Shadow' ) },
						{ value: 'lift', label: __( 'Lift' ) },
						{ value: 'lift-shadow', label: __( 'Lift w/ shadow' ) },
						{ value: 'lift-staggered', label: __( 'Staggered lift' ) },
						{ value: 'lift-shadow-staggered', label: __( 'Staggered lift w/ shadow' ) },
						{ value: 'scale', label: __( 'Scale' ) },
						{ value: 'scale-shadow', label: __( 'Scale w/ shadow' ) },
						{ value: 'lower', label: __( 'Lower' ) },
					] }
					onChange={ onChangeHoverEffect }
				/>
			}
			{ onChangeGrayscaleEffect &&
				<ToggleControl
					label={ __( 'Grayscale Hover Effect' ) }
					checked={ grayscaleEffect }
					onChange={ onChangeGrayscaleEffect }
				/>
			}
			{ props.children }
		</PanelBody>
	)
}

export default PanelEffectsSettings
