import './custom-css'
import './effects'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignBasic from './images/basic.png'
import ImageDesignBox from './images/box.png'
import ImageDesignCaptioned from './images/captioned.png'
import ImageDesignFade from './images/fade.png'
import ImageDesignLine from './images/line.png'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.image-box.edit.designs', 'stackable/image-box', ret => {
		return [ ...ret,
			{
				label: __( 'Basic' ), value: 'basic', image: ImageDesignBasic,
			},
			{
				label: __( 'Box' ), value: 'box', image: ImageDesignBox,
			},
			{
				label: __( 'Captioned' ), value: 'captioned', image: ImageDesignCaptioned,
			},
			{
				label: __( 'Fade' ), value: 'fade', image: ImageDesignFade,
			},
			{
				label: __( 'Line' ), value: 'line', image: ImageDesignLine,
			},
		]
	} )

	addFilter( 'stackable.image-box.edit.show', 'stackable/image-box', ( show, design ) => {
		if ( [ 'box', 'captioned', 'fade' ].includes( design ) ) {
			return {
				...show,
				verticalAlignmentToolbar: false,
			}
		}
		return show
	} )

	addFilter( 'stackable.image-box.edit.inspector.after', 'stackable/image-box', ( output, design, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	} )
}

addFilter( 'stackable.image-box.mainclasses', 'stackable/image-box', ( classes, design, props ) => {
	const { horizontalAlign } = props.attributes
	if ( design === 'line' ) {
		return {
			...classes,
			[ `ugb-image-box--align-${ horizontalAlign }` ]: horizontalAlign,
		}
	}
	return classes
} )

addFilter( 'stackable.image-box.itemclasses', 'stackable/image-box', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )
