import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-cta'
const defaultCSS = `/* Call to action container */
.ugb-cta {
\t
}

/* Call to action content wrapper */
.ugb-cta .ugb-content-wrapper {
\t
}

/* Call to action title */
.ugb-cta .ugb-cta__title {
\t
}

/* Call to action description */
.ugb-cta .ugb-cta__description {
\t
}

/* Call to action button */
.ugb-cta .ugb-button {
\t
}

/* Call to action button text */
.ugb-cta .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.cta.edit.inspector.after', 'stackable/cta', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.cta.edit.output.before', 'stackable/cta', outputStyle )
addFilter( 'stackable.cta.save.output.before', 'stackable/cta', outputStyle )

addFilter( 'stackable.cta.mainclasses', 'stackable/cta', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
