import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-countup'
const defaultCSS = `/* Count up container */
.ugb-countup {
\t
}

/* Count up item */
.ugb-countup .ugb-countup__item {
\t
}

/* Count up title */
.ugb-countup .ugb-countup__title {
\t
}

/* Count up number counter */
.ugb-countup .ugb-countup__counter {
\t
}

/* Count up description */
.ugb-countup .ugb-countup__description {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.count-up.edit.inspector.after', 'stackable/count-up', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.count-up.edit.output.before', 'stackable/count-up', outputStyle )
addFilter( 'stackable.count-up.save.output.before', 'stackable/count-up', outputStyle )

addFilter( 'stackable.count-up.mainclasses', 'stackable/count-up', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
