import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignHorizontalCard from './images/horizontal-card.png'
import ImageDesignImageCard from './images/image-card.png'
import ImageDesignPortfolio from './images/portfolio.png'
import ImageDesignPortfolio2 from './images/portfolio2.png'
import ImageDesignVerticalCard from './images/vertical-card.png'
import ImageDesignVerticalCard2 from './images/vertical-card2.png'
import { isPro } from 'stackable'

if ( isPro ) {
	addFilter( 'stackable.blog-posts.edit.designs', 'stackable/blog-posts', ret => {
		return [ ...ret,
			{
				label: __( 'Portfolio' ), value: 'portfolio', image: ImageDesignPortfolio,
			},
			{
				label: __( 'Portfolio 2' ), value: 'portfolio2', image: ImageDesignPortfolio2,
			},
			{
				label: __( 'Vertical Card' ), value: 'vertical-card', image: ImageDesignVerticalCard,
			},
			{
				label: __( 'Horizontal Card' ), value: 'horizontal-card', image: ImageDesignHorizontalCard,
			},
			{
				label: __( 'Vertical Card 2' ), value: 'vertical-card2', image: ImageDesignVerticalCard2,
			},
			{
				label: __( 'Image Card' ), value: 'image-card', image: ImageDesignImageCard,
			},
		]
	} )
}
