import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.image-box.edit.image-hover-effects', 'stackable/image-box', effects => {
		return [
			...effects,
			{ label: __( 'Tilt' ), value: 'tilt' },
			{ label: __( 'Zoom & Tilt' ), value: 'zoom-tilt' },
			{ label: __( 'Up' ), value: 'up' },
			{ label: __( 'Down' ), value: 'down' },
			{ label: __( 'Left' ), value: 'left' },
			{ label: __( 'Right' ), value: 'right' },
			{ label: __( 'Blur In' ), value: 'blur-in' },
			{ label: __( 'Blur Out' ), value: 'blur-out' },
			{ label: __( 'Grayscale In' ), value: 'grayscale-in' },
			{ label: __( 'Grayscale Out' ), value: 'grayscale-out' },
		]
	} )
}
