import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-notification'
const defaultCSS = `/* Notification container */
.ugb-notification {
\t
}

/* Notification text */
.ugb-notification p {
\t
}

/* Notification close button */
.ugb-notification .ugb-notification__close-button {
\t
}

/* Notification close button icon */
.ugb-notification .ugb-notification__close-button svg {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.notification.edit.inspector.after', 'stackable/notification', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.notification.edit.output.before', 'stackable/notification', outputStyle )
addFilter( 'stackable.notification.save.output.before', 'stackable/notification', outputStyle )

addFilter( 'stackable.notification.mainclasses', 'stackable/notification', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
