import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-feature'
const defaultCSS = `/* Feature container */
.ugb-feature {
\t
}

/* Feature image */
.ugb-feature .ugb-feature__image {
\t
}

/* Feature content wrapper */
.ugb-feature .ugb-feature__content {
\t
}

/* Feature title */
.ugb-feature .ugb-feature__title {
\t
}

/* Feature description */
.ugb-feature .ugb-feature__description {
\t
}

/* Feature button */
.ugb-feature .ugb-button {
\t
}

/* Feature button text */
.ugb-feature .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.feature.edit.inspector.after', 'stackable/feature', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.feature.edit.output.before', 'stackable/feature', outputStyle )
addFilter( 'stackable.feature.save.output.before', 'stackable/feature', outputStyle )

addFilter( 'stackable.feature.mainclasses', 'stackable/feature', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
