import './custom-css'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignCenteredQuote from './images/centered-quote.png'
import ImageDesignHighlight from './images/highlight.png'
import ImageDesignHude from './images/huge.png'
import isDarkColor from 'is-dark-color'
import { isPro } from 'stackable'

if ( isPro ) {
	addFilter( 'stackable.blockquote.edit.designs', 'stackable/blockquote', ret => {
		return [ ...ret,
			{
				label: __( 'Centered Quote' ), value: 'centered-quote', image: ImageDesignCenteredQuote,
			},
			{
				label: __( 'Huge' ), value: 'huge', image: ImageDesignHude,
			},
			{
				label: __( 'Highlight' ), value: 'highlight', image: ImageDesignHighlight,
			},
		]
	} )
}

addFilter( 'stackable.blockquote.edit.output', 'stackable/blockquote', ( output, design ) => {
	if ( design === 'highlight' ) {
		return (
			<span className="ugb-blockquote__highlight-wrap">
				{ output }
			</span>
		)
	}
	return output
} )

addFilter( 'stackable.blockquote.save.output', 'stackable/blockquote', ( output, design ) => {
	if ( design === 'highlight' ) {
		return (
			<span className="ugb-blockquote__highlight-wrap">
				{ output }
			</span>
		)
	}
	return output
} )

addFilter( 'stackable.blockquote.mainclasses', 'stackable/blockquote', ( classes, design, props ) => {
	const { color } = props.attributes
	if ( design === 'highlight' ) {
		return {
			...classes,
			'ugb-blockquote--dark-text': color ? isDarkColor( color ) : undefined,
		}
	}
	return classes
} )

addFilter( 'stackable.blockquote.styles', 'stackable/blockquote', ( styles, design, props ) => {
	const { color } = props.attributes
	if ( design === 'highlight' ) {
		return {
			...styles,
			main: {
				...styles.main,
				'--ugb-box-color': color,
			},
		}
	}
	return styles
} )
