import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-button-wrapper'
const defaultCSS = `/* Button container */
.ugb-button-wrapper {
\t
}

/* 1st Button */
.ugb-button-wrapper > *:nth-child(1) .ugb-button {
\t
}

/* 1st Button text */
.ugb-button-wrapper > *:nth-child(1) .ugb-button--inner {
\t
}

/* 1st Button hovered */
.ugb-button-wrapper > *:nth-child(1) .ugb-button:hover {
\t
}

/* 1st Button hovered text */
.ugb-button-wrapper > *:nth-child(1) .ugb-button:hover .ugb-button--inner {
\t
}

/* 2nd Button */
.ugb-button-wrapper > *:nth-child(2) .ugb-button {
\t
}

/* 2nd Button text */
.ugb-button-wrapper > *:nth-child(2) .ugb-button--inner {
\t
}

/* 2nd Button hovered */
.ugb-button-wrapper > *:nth-child(2) .ugb-button:hover {
\t
}

/* 2nd Button hovered text */
.ugb-button-wrapper > *:nth-child(2) .ugb-button:hover .ugb-button--inner {
\t
}

/* 3rd Button */
.ugb-button-wrapper > *:nth-child(3) .ugb-button {
\t
}

/* 3rd Button text */
.ugb-button-wrapper > *:nth-child(3) .ugb-button--inner {
\t
}

/* 3rd Button hovered */
.ugb-button-wrapper > *:nth-child(3) .ugb-button:hover {
\t
}

/* 3rd Button hovered text */
.ugb-button-wrapper > *:nth-child(3) .ugb-button:hover .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.button.edit.inspector.after', 'stackable/button', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	} )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.button.edit.output.after', 'stackable/button', outputStyle, 11 )
addFilter( 'stackable.button.save.output.after', 'stackable/button', outputStyle, 11 )

addFilter( 'stackable.button.mainclasses', 'stackable/button', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
