import './custom-css'
import { ColorPaletteControl, PanelAdvancedSettings } from '@stackable/components'
import { layer2s, layer3s } from './layers'
import { RangeControl, ToggleControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'

if ( isPro ) {
	addFilter( 'stackable.separator.edit.inspector.after', 'stackable/separator', ( output, design, props ) => {
		const { attributes, setAttributes } = props
		const {
			layer2 = false,
			layer2Color = '',
			layer2Height = 1,
			layer2Width = 1,
			layer2Flip = false,
			layer2Opacity = 0.5,
			layer3 = false,
			layer3Color = '',
			layer3Height = 1,
			layer3Width = 1,
			layer3Flip = false,
			layer3Opacity = 0.5,
		} = attributes

		return (
			<Fragment>
				<PanelAdvancedSettings
					title={ __( 'Layer 2' ) }
					checked={ layer2 }
					onChange={ layer2 => setAttributes( { layer2 } ) }
				>
					<ColorPaletteControl
						label={ __( 'Layer Color' ) }
						value={ layer2Color }
						onChange={ layer2Color => setAttributes( { layer2Color } ) }
					/>
					<RangeControl
						label={ __( 'Layer Height' ) }
						value={ layer2Height }
						min="0"
						max="2"
						step="0.01"
						onChange={ layer2Height => setAttributes( { layer2Height } ) }
					/>
					<RangeControl
						label={ __( 'Layer Width' ) }
						value={ layer2Width }
						min="1"
						max="4"
						step="0.1"
						onChange={ layer2Width => setAttributes( { layer2Width } ) }
					/>
					<ToggleControl
						label={ __( 'Flip Horizontally' ) }
						checked={ layer2Flip }
						onChange={ layer2Flip => setAttributes( { layer2Flip } ) }
					/>
					<RangeControl
						label={ __( 'Opacity' ) }
						value={ layer2Opacity }
						min="0"
						max="1"
						step="0.1"
						onChange={ layer2Opacity => setAttributes( { layer2Opacity } ) }
					/>
				</PanelAdvancedSettings>
				<PanelAdvancedSettings
					title={ __( 'Layer 3' ) }
					checked={ layer3 }
					onChange={ layer3 => setAttributes( { layer3 } ) }
				>
					<ColorPaletteControl
						label={ __( 'Layer Color' ) }
						value={ layer3Color }
						onChange={ layer3Color => setAttributes( { layer3Color } ) }
					/>
					<RangeControl
						label={ __( 'Layer Height' ) }
						value={ layer3Height }
						min="0"
						max="2"
						step="0.01"
						onChange={ layer3Height => setAttributes( { layer3Height } ) }
					/>
					<RangeControl
						label={ __( 'Layer Width' ) }
						value={ layer3Width }
						min="1"
						max="4"
						step="0.1"
						onChange={ layer3Width => setAttributes( { layer3Width } ) }
					/>
					<ToggleControl
						label={ __( 'Flip Horizontally' ) }
						checked={ layer3Flip }
						onChange={ layer3Flip => setAttributes( { layer3Flip } ) }
					/>
					<RangeControl
						label={ __( 'Opacity' ) }
						value={ layer3Opacity }
						min="0"
						max="1"
						step="0.1"
						onChange={ layer3Opacity => setAttributes( { layer3Opacity } ) }
					/>
				</PanelAdvancedSettings>
			</Fragment>
		)
	} )
}

const layer2Output = ( output, design, props ) => {
	const {
		layer2 = false,
		layer2Color = '',
		layer2Height = 1,
		layer2Width = 1,
		layer2Flip = false,
		layer2Opacity = 0.5,
	} = props.attributes

	if ( layer2 ) {
		const styles = {
			transform: '',
			fill: layer2Color ? layer2Color : undefined,
			opacity: layer2Opacity,
		}

		styles.transform += layer2Width ? ` scaleX(${ layer2Width })` : ''
		styles.transform += layer2Flip ? ` scaleX(-1)` : ''
		styles.transform += layer2Height ? ` scaleY(${ layer2Height })` : ''

		const LayerSVG = layer2s[ design ]

		return (
			<Fragment>
				{ output }
				<LayerSVG
					preserveAspectRatio="none"
					className="ugb-separator__layer-2"
					style={ styles }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.edit.output.layers', 'stackable/separator', layer2Output, 7 )
addFilter( 'stackable.separator.save.output.layers', 'stackable/separator', layer2Output, 7 )

const layer3Output = ( output, design, props ) => {
	const {
		layer3 = false,
		layer3Color = '',
		layer3Height = 1,
		layer3Width = 1,
		layer3Flip = false,
		layer3Opacity = 0.5,
	} = props.attributes

	if ( layer3 ) {
		const styles = {
			transform: '',
			fill: layer3Color ? layer3Color : undefined,
			opacity: layer3Opacity,
		}

		styles.transform += layer3Width ? ` scaleX(${ layer3Width })` : ''
		styles.transform += layer3Flip ? ` scaleX(-1)` : ''
		styles.transform += layer3Height ? ` scaleY(${ layer3Height })` : ''

		const LayerSVG = layer3s[ design ]

		return (
			<Fragment>
				{ output }
				<LayerSVG
					preserveAspectRatio="none"
					className="ugb-separator__layer-3"
					style={ styles }
				/>
			</Fragment>
		)
	}
	return output
}

addFilter( 'stackable.separator.edit.output.layers', 'stackable/separator', layer3Output, 8 )
addFilter( 'stackable.separator.save.output.layers', 'stackable/separator', layer3Output, 8 )
