import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-blog-posts'
const defaultCSS = `/* Blog Posts container */
.ugb-blog-posts {
\t
}

/* Blog Post individual item */
.ugb-blog-posts .ugb-blog-posts__item {
\t
}

/* Blog Post category container */
.ugb-blog-posts .ugb-blog-posts__category-list {
\t
}

/* Blog Post category item */
.ugb-blog-posts .ugb-blog-posts__category-list a {
\t
}

/* Blog Post featured image */
.ugb-blog-posts .ugb-blog-posts__featured-image {
\t
}

/* Blog Post meta info */
.ugb-blog-posts .ugb-blog-posts__meta {
\t
}

/* Blog Post titles */
.ugb-blog-posts .ugb-blog-posts__title a {
\t
}

/* Blog Post excerpt container */
.ugb-blog-posts .ugb-blog-posts__excerpt {
\t
}

/* Blog Post excerpt text */
.ugb-blog-posts .ugb-blog-posts__excerpt p {
\t
}

/* Blog Post continue reading link */
.ugb-blog-posts .ugb-blog-posts__read_more a {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.blog-posts.edit.inspector.after', 'stackable/blog-posts', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	} )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.blog-posts.edit.output.before', 'stackable/blog-posts', outputStyle )
// addFilter( 'stackable.blog-posts.save.output.before', 'stackable/blog-posts', outputStyle )

addFilter( 'stackable.blog-posts.mainclasses', 'stackable/blog-posts', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
