import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignHorizontal from './images/horizontal.png'
import ImageDesignLargeMid from './images/large-mid.png'
import ImageDesignZigzag from './images/zigzag.png'
import { omit } from 'lodash'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.feature-grid.edit.designs', 'stackable/feature-grid', ret => {
		return [ ...ret,
			{
				label: __( 'Horizontal' ), value: 'horizontal', image: ImageDesignHorizontal,
			},
			{
				label: __( 'Large Mid' ), value: 'large-mid', image: ImageDesignLargeMid,
			},
			{
				label: __( 'Zigzag' ), value: 'zigzag', image: ImageDesignZigzag,
			},
		]
	} )

	addFilter( 'stackable.feature-grid.edit.inspector.after', 'stackable/feature-grid', ( output, design, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	} )
}

addFilter( 'stackable.feature-grid.edit.show', 'stackable/feature-grid', ( show, design ) => {
	if ( design === 'horizontal' ) {
		return {
			...show,
			imageSize: false,
		}
	}
	return show
} )

addFilter( 'stackable.feature-grid.itemclasses', 'stackable/feature-grid', ( classes, design, i, props ) => {
	const { shadow } = props.attributes

	if ( design === 'large-mid' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: i === 2,
			'ugb--shadow-0': i !== 2,
		}
	}
	return classes
} )

addFilter( 'stackable.feature-grid.itemclasses', 'stackable/feature-grid', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )

addFilter( 'stackable.feature-grid.itemstyles', 'stackable/feature-grid', ( styles, design ) => {
	if ( design === 'horizontal' ) {
		return {
			...styles,
			image: omit( styles.image, [ 'width' ] ),
		}
	}
	return styles
} )
