import './custom-css'
import { addFilter, applyFilters } from '@wordpress/hooks'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import classnames from 'classnames'
import { Fragment } from '@wordpress/element'
import ImageDesignHalf from './images/half.png'
import ImageDesignOverlap from './images/overlap.png'
import ImageDesignOverlap2 from './images/overlap2.png'
import ImageDesignOverlap3 from './images/overlap3.png'
import { omit } from 'lodash'
import { PanelBackgroundSettings } from '@stackable/components'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.feature.edit.designs', 'stackable/count-up', ret => {
		return [ ...ret,
			{
				label: __( 'Half' ), value: 'half', image: ImageDesignHalf,
			},
			{
				label: __( 'Overlap' ), value: 'overlap', image: ImageDesignOverlap,
			},
			{
				label: __( 'Overlap 2' ), value: 'overlap2', image: ImageDesignOverlap2,
			},
			{
				label: __( 'Overlap 3' ), value: 'overlap3', image: ImageDesignOverlap3,
			},
		]
	} )

	addFilter( 'stackable.feature.edit.inspector.after', 'stackable/feature', ( output, design, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		if ( ! [ 'overlap', 'overlap2', 'overlap3' ].includes( design ) ) {
			return (
				<Fragment>
					{ output }
					<PanelEffectsSettings
						hoverEffect={ hoverEffect }
						onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
					/>
				</Fragment>
			)
		}
		return output
	} )
}

addFilter( 'stackable.feature.edit.output', 'stackable/feature', ( output, comps, props ) => {
	const {
		design,
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
		borderRadius,
		shadow,
	} = props.attributes

	if ( design === 'half' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses', {
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				{ comps.imageBGComp }
			</div>
		)
	}

	if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses', {
			[ `ugb--shadow-${ shadow }` ]: shadow,
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			borderRadius,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				{ comps.imageBGComp }
			</div>
		)
	}

	return output
} )

addFilter( 'stackable.feature.save.output', 'stackable/feature', ( output, comps, props ) => {
	const {
		design,
		imageUrl,
		imageSize,
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
		borderRadius,
		shadow,
	} = props.attributes

	if ( design === 'half' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses', {
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			backgroundColor: backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		const imageStyles = {
			backgroundImage: imageUrl ? `url(${ imageUrl })` : undefined,
			minHeight: imageSize,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				<div className="ugb-feature__image" style={ imageStyles } />
			</div>
		)
	}

	if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		const contentClasses = classnames( [
			'ugb-feature__content',
		], applyFilters( 'stackable.feature.contentclasses', {
			[ `ugb--shadow-${ shadow }` ]: shadow,
			[ `ugb--has-background-gradient` ]: backgroundColorType === 'gradient',
		}, design, props ) )
		const contentStyle = {
			borderRadius,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			'--ugb-background-color': backgroundColorType === 'gradient' ? backgroundColor : undefined,
			'--ugb-background-color2': backgroundColorType === 'gradient' && backgroundColor2 ? backgroundColor2 : undefined,
			'--ugb-background-direction': backgroundColorType === 'gradient' ? `${ backgroundColorDirection }deg` : undefined,
		}
		const imageStyle = {
			backgroundImage: imageUrl ? `url(${ imageUrl })` : undefined,
			minHeight: imageSize,
			borderRadius,
		}
		return (
			<div className="ugb-content-wrapper">
				<div className={ contentClasses } style={ contentStyle }>
					{ comps.titleComp }
					{ comps.descriptionComp }
					{ comps.buttonComp }
				</div>
				<div className="ugb-feature__image" style={ imageStyle } />
			</div>
		)
	}

	return output
} )

addFilter( 'stackable.feature.styles', 'stackable/feature', ( styles, design, props ) => {
	const { imageSize, borderRadius } = props.attributes
	if ( design === 'half' ) {
		return {
			...styles,
			image: {
				...styles.image,
				minHeight: imageSize,
			},
		}
	} else if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		return {
			...styles,
			image: {
				...styles.image,
				minHeight: imageSize,
				borderRadius: borderRadius,
			},
			main: omit( styles.main, [
				'backgroundColor',
				'backgroundImage',
				'backgroundAttachment',
				'--ugb-background-color',
				'--ugb-background-color2',
				'--ugb-background-direction',
				'borderRadius',
			] ),
		}
	}
	return styles
} )

addFilter( 'stackable.feature.mainclasses', 'stackable/feature', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'overlap' || design === 'overlap2' || design === 'overlap3' ) {
		return omit( classes, [
			'ugb--has-background',
			'ugb--has-background-image',
			`ugb--shadow-${ shadow }`,
			'ugb--has-background-gradient',
			'ugb--has-background-video',
		] )
	}

	return classes
} )

addFilter( 'stackable.feature.edit.show', 'stackable/feature', ( show, design ) => {
	if ( [ 'half', 'overlap', 'overlap2', 'overlap3' ].includes( design ) ) {
		return {
			...show,
			background: false,
		}
	}
	return show
} )

addFilter( 'stackable.feature.edit.inspector', 'stackable/feature', ( output, design, props ) => {
	const { setAttributes } = props
	const {
		backgroundColorType = '',
		backgroundColor,
		backgroundColor2,
		backgroundColorDirection = 0,
	} = props.attributes
	if ( [ 'half', 'overlap', 'overlap2', 'overlap3' ].includes( design ) ) {
		return (
			<PanelBackgroundSettings
				backgroundColorType={ backgroundColorType }
				backgroundColor={ backgroundColor }
				backgroundColor2={ backgroundColor2 }
				backgroundColorDirection={ backgroundColorDirection }
				onChangeBackgroundColor={ backgroundColor => setAttributes( { backgroundColor } ) }
				onChangeBackgroundColorType={ backgroundColorType => setAttributes( { backgroundColorType } ) }
				onChangeBackgroundColor2={ backgroundColor2 => setAttributes( { backgroundColor2 } ) }
				onChangeBackgroundColorDirection={ backgroundColorDirection => setAttributes( { backgroundColorDirection } ) }
			/>
		)
	}
} )

addFilter( 'stackable.feature.mainclasses', 'stackable/feature', ( classes, design, props ) => {
	const { hoverEffect = '' } = props.attributes
	if ( [ 'basic', 'half' ].includes( design ) ) {
		return {
			...classes,
			[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
		}
	}
	return classes
} )

