import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-team-member'
const defaultCSS = `/* Team Member container */
.ugb-team-member {
\t
}

/* Team Member item */
.ugb-team-member .ugb-team-member__item {
	\t
}

/* Team Member image */
.ugb-team-member .ugb-team-member__image {
\t
}

/* Team Member content wrapper */
.ugb-team-member .ugb-team-member__content {
\t
}

/* Team Member name */
.ugb-team-member .ugb-team-member__name {
\t
}

/* Team Member position */
.ugb-team-member .ugb-team-member__position {
\t
}

/* Team Member description */
.ugb-team-member .ugb-team-member__description {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.team-member.edit.inspector.after', 'stackable/team-member', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.team-member.edit.output.before', 'stackable/team-member', outputStyle )
addFilter( 'stackable.team-member.save.output.before', 'stackable/team-member', outputStyle )

addFilter( 'stackable.team-member.mainclasses', 'stackable/team-member', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
