import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-expand'
const defaultCSS = `/* Expand container */
.ugb-expand {
\t
}

/* Unexpanded text */
.ugb-expand .ugb-expand__less-text p {
\t
}

/* Expanded text */
.ugb-expand .ugb-expand__more-text p {
\t
}

/* Toggle text */
.ugb-expand .ugb-expand__toggle,
.ugb-expand .ugb-expand__more-toggle-text,
.ugb-expand .ugb-expand__less-toggle-text {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.expand.edit.inspector.after', 'stackable/expand', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.expand.edit.output.before', 'stackable/expand', outputStyle )
addFilter( 'stackable.expand.save.output.before', 'stackable/expand', outputStyle )

addFilter( 'stackable.expand.mainclasses', 'stackable/expand', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
