import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-image-box'
const defaultCSS = `/* Image box container */
.ugb-image-box {
\t
}

/* Image box item */
.ugb-image-box .ugb-image-box__item {
\t
}

/* Image box overlay */
.ugb-image-box .ugb-image-box__overlay {
\t
}

/* Image box content wrapper */
.ugb-image-box .ugb-image-box__content {
\t
}

/* Image box title */
.ugb-image-box .ugb-image-box__title {
\t
}

/* Image box description */
.ugb-image-box .ugb-image-box__description {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.image-box.edit.inspector.after', 'stackable/image-box', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.image-box.edit.output.before', 'stackable/image-box', outputStyle )
addFilter( 'stackable.image-box.save.output.before', 'stackable/image-box', outputStyle )

addFilter( 'stackable.image-box.mainclasses', 'stackable/image-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
