import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignColored from './images/colored.png'
import ImageDesignCompact from './images/compact.png'
import ImageDesignSectioned from './images/sectioned.png'
import { omit } from 'lodash'
import PanelEffectsSettings from '../../components/panel-effects-settings'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.pricing-box.edit.designs', 'stackable/pricing-box', ret => {
		return [ ...ret,
			{
				label: __( 'Compact' ), value: 'compact', image: ImageDesignCompact,
			},
			{
				label: __( 'Sectioned' ), value: 'sectioned', image: ImageDesignColored,
			},
			{
				label: __( 'Colored' ), value: 'colored', image: ImageDesignSectioned,
			},
		]
	} )

	addFilter( 'stackable.pricing-box.edit.inspector.after', 'stackable/pricing-box', ( output, design, props ) => {
		const { setAttributes } = props
		const { hoverEffect = '' } = props.attributes
		return (
			<Fragment>
				{ output }
				<PanelEffectsSettings
					hoverEffect={ hoverEffect }
					onChangeHoverEffect={ hoverEffect => setAttributes( { hoverEffect } ) }
				/>
			</Fragment>
		)
	} )
}

addFilter( 'stackable.pricing-box.edit.output', 'stackable/pricing-box', ( output, design, comps, i, props ) => {
	if ( design === 'compact' ) {
		return (
			<Fragment>
				<div className="ugb-pricing-box__header">
					{ comps.imageComp }
					<div className="ugb-pricing-box__side">
						{ comps.titleComp }
						{ comps.priceComp }
					</div>
				</div>
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'sectioned' ) {
		return (
			<Fragment>
				{ comps.titleComp }
				{ comps.imageBGComp }
				{ comps.priceComp }
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'colored' ) {
		const index = i === 1 ? '' : i
		const highlightColor = props.attributes[ `highlightColor${ index }` ] || ''

		const headerStyles = {
			backgroundColor: highlightColor ? highlightColor : undefined,
		}

		return (
			<Fragment>
				<div className="ugb-pricing-box__header" style={ headerStyles }>
					{ comps.imageBGComp }
					{ comps.titleComp }
					{ comps.priceComp }
					{ comps.buttonComp }
				</div>
				{ comps.descriptionComp }
			</Fragment>
		)
	}
	return output
} )

addFilter( 'stackable.pricing-box.save.output', 'stackable/pricing-box', ( output, design, comps, i, props ) => {
	if ( design === 'compact' ) {
		return (
			<Fragment>
				<div className="ugb-pricing-box__header">
					{ comps.imageComp }
					<div className="ugb-pricing-box__side">
						{ comps.titleComp }
						{ comps.priceComp }
					</div>
				</div>
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'sectioned' ) {
		return (
			<Fragment>
				{ comps.titleComp }
				{ comps.imageBGComp }
				{ comps.priceComp }
				{ comps.descriptionComp }
				{ comps.buttonComp }
			</Fragment>
		)
	} else if ( design === 'colored' ) {
		const index = i === 1 ? '' : i
		const highlightColor = props.attributes[ `highlightColor${ index }` ] || ''

		const headerStyles = {
			backgroundColor: highlightColor ? highlightColor : undefined,
		}

		return (
			<Fragment>
				<div className="ugb-pricing-box__header" style={ headerStyles }>
					{ comps.imageBGComp }
					{ comps.titleComp }
					{ comps.priceComp }
					{ comps.buttonComp }
				</div>
				{ comps.descriptionComp }
			</Fragment>
		)
	}
	return output
} )

addFilter( 'stackable.pricing-box.styles', 'stackable/pricing-box', ( styles, design, i, props ) => {
	if ( design === 'sectioned' ) {
		const index = i === 1 ? '' : i
		const highlightColor = props.attributes[ `highlightColor${ index }` ] || ''

		styles.item = omit( styles.item, [ 'backgroundColor' ] )
		styles.title.backgroundColor = highlightColor ? highlightColor : undefined
	} else if ( design === 'colored' ) {
		styles.item = omit( styles.item, [ 'backgroundColor' ] )
	}
	return styles
} )

addFilter( 'stackable.pricing-box.itemclasses', 'stackable/pricing-box', ( classes, design, i, props ) => {
	const {
		hoverEffect = '',
	} = props.attributes

	return {
		...classes,
		[ `ugb--hover-${ hoverEffect }` ]: hoverEffect,
	}
} )
