import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-feature-grid'
const defaultCSS = `/* Feature Grid container */
.ugb-feature-grid {
\t
}

/* Feature Grid item */
.ugb-feature-grid .ugb-feature-grid__item {
\t
}

/* Feature Grid content wrapper */
.ugb-feature-grid .ugb-feature-grid__content {
\t
}

/* Feature Grid image wrapper */
.ugb-feature-grid .ugb-feature-grid__image {
\t
}

/* Feature Grid image */
.ugb-feature-grid .ugb-feature-grid__image img {
\t
}

/* Feature Grid title */
.ugb-feature-grid .ugb-feature-grid__title {
\t
}

/* Feature Grid description */
.ugb-feature-grid .ugb-feature-grid__description {
\t
}

/* Feature Grid button */
.ugb-feature-grid .ugb-button {
\t
}

/* Feature Grid button text */
.ugb-feature-grid .ugb-button span {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.feature-grid.edit.inspector.after', 'stackable/feature-grid', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.feature-grid.edit.output.before', 'stackable/feature-grid', outputStyle )
addFilter( 'stackable.feature-grid.save.output.before', 'stackable/feature-grid', outputStyle )

addFilter( 'stackable.feature-grid.mainclasses', 'stackable/feature-grid', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
