import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-header'
const defaultCSS = `/* Header container */
.ugb-header {
\t
}

/* Header content wrapper */
.ugb-header .ugb-content-wrapper {
\t
}

/* Header title */
.ugb-header .ugb-header__title {
\t
}

/* Header subtitle */
.ugb-header .ugb-header__subtitle {
\t
}

/* Header button #1 */
.ugb-header .ugb-header__buttons > div:nth-child(1) .ugb-button {
\t
}

/* Header button #1 text */
.ugb-header .ugb-header__buttons > div:nth-child(1) .ugb-button .ugb-button--inner {
\t
}

/* Header button #2 */
.ugb-header .ugb-header__buttons > div:nth-child(2) .ugb-button {
\t
}

/* Header button #2 text */
.ugb-header .ugb-header__buttons > div:nth-child(2) .ugb-button .ugb-button--inner {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.header.edit.inspector.after', 'stackable/header', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.header.edit.output.before', 'stackable/header', outputStyle )
addFilter( 'stackable.header.save.output.before', 'stackable/header', outputStyle )

addFilter( 'stackable.header.mainclasses', 'stackable/header', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
