import './custom-css'
import { isPro, planName } from 'stackable'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import ImageDesignBackground from './images/background.png'
import ImageDesignBasic2 from './images/basic2.png'
import ImageDesignBubble from './images/bubble.png'
import ImageDesignVertical from './images/vertical.png'
import ImageDesignVerticalInverse from './images/vertical-inverse.png'
import { omit } from 'lodash'

if ( isPro && planName !== 'starter' ) {
	addFilter( 'stackable.testimonial.edit.designs', 'stackable/testimonial', ret => {
		return [ ...ret,
			{
				label: __( 'Basic 2' ), value: 'basic2', image: ImageDesignBasic2,
			},
			{
				label: __( 'Bubble' ), value: 'bubble', image: ImageDesignBubble,
			},
			{
				label: __( 'Background' ), value: 'background', image: ImageDesignBackground,
			},
			{
				label: __( 'Vertical' ), value: 'vertical', image: ImageDesignVertical,
			},
			{
				label: __( 'Vertical Inverse' ), value: 'vertical-inverse', image: ImageDesignVerticalInverse,
			},
		]
	} )
}

addFilter( 'stackable.testimonial.bodyclasses', 'stackable/testimonial', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'bubble' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )

addFilter( 'stackable.testimonial.itemclasses', 'stackable/testimonial', ( classes, design, props ) => {
	const { shadow } = props.attributes
	if ( design === 'bubble' ) {
		return omit( classes, [ `ugb--shadow-${ shadow }` ] )
	}
	if ( design === 'background' ) {
		return {
			...classes,
			[ `ugb--shadow-${ shadow }` ]: true,
		}
	}
	return classes
} )

addFilter( 'stackable.testimonial.styles', 'stackable/testimonial', ( styles, design, props ) => {
	const { borderRadius, backgroundColor } = props.attributes
	if ( design === 'bubble' ) {
		styles.item = omit( styles.item, [ 'borderRadius', 'backgroundColor' ] )
		styles.bodyWrapper = {
			...styles.bodyWrapper,
			borderRadius: borderRadius ? borderRadius : undefined,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			color: backgroundColor ? backgroundColor : undefined,
		}
	}
	if ( design === 'background' ) {
		styles.item = {
			...styles.item,
			borderRadius,
		}
	}
	if ( design === 'vertical' || design === 'vertical-inverse' ) {
		styles.bodyWrapper = {
			...styles.bodyWrapper,
			backgroundColor: backgroundColor ? backgroundColor : undefined,
			color: backgroundColor ? backgroundColor : undefined,
		}
	}
	return styles
} )

addFilter( 'stackable.testimonial.show', 'stackable/testimonial', ( show, design ) => {
	if ( [ 'basic2', 'bubble', 'vertical', 'vertical-inverse' ].includes( design ) ) {
		show.backgroundColor = true
	}
	return show
} )
