import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-testimonial'
const defaultCSS = `/* Testimonial container */
.ugb-testimonial {
\t
}

/* Testimonial item */
.ugb-testimonial .ugb-testimonial__item {
	\t
}

/* Testimonial body text */
.ugb-testimonial .ugb-testimonial__body {
\t
}

/* Testimonial person wrapper */
.ugb-testimonial .ugb-testimonial__person {
\t
}

/* Testimonial image */
.ugb-testimonial .ugb-testimonial__image {
\t
}

/* Testimonial name */
.ugb-testimonial .ugb-testimonial__name {
\t
}

/* Testimonial position */
.ugb-testimonial .ugb-testimonial__position {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.testimonial.edit.inspector.after', 'stackable/testimonial', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.testimonial.edit.output.before', 'stackable/testimonial', outputStyle )
addFilter( 'stackable.testimonial.save.output.before', 'stackable/testimonial', outputStyle )

addFilter( 'stackable.testimonial.mainclasses', 'stackable/testimonial', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
