import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-accordion'
const defaultCSS = `/* Accordion container */
.ugb-accordion {
\t
}

/* Accordion heading */
.ugb-accordion__heading {
\t
}

/* Accordion heading text */
.ugb-accordion__heading h4 {
\t
}

/* Accordion arrow */
.ugb-accordion__heading svg {
\t
}

/* Body */
.ugb-accordion__text {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.accordion.edit.inspector.after', 'stackable/accordion', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	} )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.accordion.edit.output.before', 'stackable/accordion', outputStyle )
addFilter( 'stackable.accordion.save.output.before', 'stackable/accordion', outputStyle )

addFilter( 'stackable.accordion.mainclasses', 'stackable/accordion', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
