import './custom-css'
import { ButtonEdit, PanelButtonSettings, URLInputControl } from '@stackable/components'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignHalf from './images/half.png'
import ImageDesignHalfOverlay from './images/half-overlay.png'
import ImageDesignHuge from './images/huge.png'
import { isPro } from 'stackable'
import { ToggleControl } from '@wordpress/components'

if ( isPro ) {
	addFilter( 'stackable.header.edit.designs', 'stackable/image-box', ret => {
		return [ ...ret,
			{
				label: __( 'Half Overlay' ), value: 'half-overlay', image: ImageDesignHalfOverlay,
			},
			{
				label: __( 'Half' ), value: 'half', image: ImageDesignHalf,
			},
			{
				label: __( 'Huge' ), value: 'huge', image: ImageDesignHuge,
			},
		]
	} )

	addFilter( 'stackable.header.edit.url', 'stackable/header', ( ret, design, props ) => {
		const { setAttributes } = props
		const {
			buttonURL2 = '',
			buttonNewTab2 = false,
		} = props.attributes
		return (
			<URLInputControl
				value={ buttonURL2 }
				newTab={ buttonNewTab2 }
				onChange={ buttonURL2 => setAttributes( { buttonURL2 } ) }
				onChangeNewTab={ buttonNewTab2 => setAttributes( { buttonNewTab2 } ) }
			/>
		)
	} )

	addFilter( 'stackable.header.edit.button.props', 'stackable/header', props => {
		return {
			...props,
			title: __( 'Button #1 Settings' ),
		}
	} )

	addFilter( 'stackable.header.edit.inspector.after', 'stackable/header', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			buttonSize2,
			buttonTextColor2,
			buttonColor2,
			buttonBorderRadius2 = 4,
			buttonDesign2 = 'basic',
			buttonIcon2 = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelButtonSettings
					initialOpen={ false }
					title={ __( 'Button #2 Settings' ) }
					buttonColor={ buttonColor2 }
					buttonTextColor={ buttonTextColor2 }
					buttonSize={ buttonSize2 }
					buttonBorderRadius={ buttonBorderRadius2 }
					buttonDesign={ buttonDesign2 }
					buttonIcon={ buttonIcon2 }
					onChangeButtonColor={ buttonColor2 => setAttributes( { buttonColor2 } ) }
					onChangeButtonTextColor={ buttonTextColor2 => setAttributes( { buttonTextColor2 } ) }
					onChangeButtonSize={ buttonSize2 => setAttributes( { buttonSize2 } ) }
					onChangeButtonBorderRadius={ buttonBorderRadius2 => setAttributes( { buttonBorderRadius2 } ) }
					onChangeButtonDesign={ buttonDesign2 => setAttributes( { buttonDesign2 } ) }
					onChangeButtonIcon={ buttonIcon2 => setAttributes( { buttonIcon2 } ) }
				/>
			</Fragment>
		)
	} )

	addFilter( 'stackable.header.edit.designs.before', 'stackable/header', ( output, design, props ) => {
		const { setAttributes } = props
		const { invert } = props.attributes

		if ( [ 'half-overlay', 'half', 'huge' ].includes( design ) ) {
			return (
				<ToggleControl
					label={ __( 'Reverse Horizontally' ) }
					checked={ invert }
					onChange={ invert => setAttributes( { invert } ) }
				/>
			)
		}

		return output
	} )
}

addFilter( 'stackable.header.edit.output', 'stackable/header', ( output, design, props, comps ) => {
	const { setAttributes } = props
	const {
		contentAlign,
		buttonSize2,
		buttonTextColor2,
		buttonColor2,
		buttonText2 = '',
		buttonBorderRadius2 = 4,
		buttonDesign2 = 'basic',
		buttonIcon2 = '',
	} = props.attributes

	const button2Comp = <ButtonEdit
		size={ buttonSize2 }
		align={ contentAlign }
		color={ buttonTextColor2 }
		backgroundColor={ buttonColor2 }
		text={ buttonText2 }
		borderRadius={ buttonBorderRadius2 }
		design={ buttonDesign2 }
		icon={ buttonIcon2 }
		onChange={ buttonText2 => setAttributes( { buttonText2 } ) }
	/>

	const buttonsStyle = {
		justifyContent: contentAlign === 'right' ? 'flex-end' :
		                contentAlign === 'left' ? 'flex-start' :
		                undefined,
	}

	return <div className="ugb-content-wrapper">
		<div className="ugb-header__wrapper">
			{ comps.titleComp }
			{ comps.subtitleComp }
			<div className="ugb-header__buttons" style={ buttonsStyle }>
				{ comps.buttonComp }
				{ button2Comp }
			</div>
		</div>
	</div>
} )

addFilter( 'stackable.header.save.output', 'stackable/header', ( output, design, props, comps ) => {
	const {
		contentAlign,
		buttonSize2,
		buttonTextColor2,
		buttonColor2,
		buttonText2 = '',
		buttonBorderRadius2 = 4,
		buttonDesign2 = 'basic',
		buttonIcon2 = '',
		buttonURL2 = '',
		buttonNewTab2 = false,
	} = props.attributes

	const button2Comp = <ButtonEdit.Content
		size={ buttonSize2 }
		url={ buttonURL2 }
		newTab={ buttonNewTab2 }
		align={ contentAlign }
		color={ buttonTextColor2 }
		backgroundColor={ buttonColor2 }
		text={ buttonText2 }
		borderRadius={ buttonBorderRadius2 }
		design={ buttonDesign2 }
		icon={ buttonIcon2 }
	/>

	const buttonsStyle = {
		justifyContent: contentAlign === 'right' ? 'flex-end' :
		                contentAlign === 'left' ? 'flex-start' :
		                undefined,
	}

	const buttonArea = ! buttonText2 ? comps.buttonComp : (
		<div className="ugb-header__buttons" style={ buttonsStyle }>
			{ comps.buttonComp }
			{ button2Comp }
		</div>
	)

	if ( design === 'basic' || design === 'plain' ) {
		return <div className="ugb-content-wrapper">
			{ comps.titleComp }
			{ comps.subtitleComp }
			{ buttonArea }
		</div>
	}

	return <div className="ugb-content-wrapper">
		<div className="ugb-header__wrapper">
			{ comps.titleComp }
			{ comps.subtitleComp }
			{ buttonArea }
		</div>
	</div>
} )
