import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import PanelCustomCSSSettings from '../../components/panel-custom-css-settings'

const mainClass = 'ugb-number-box'
const defaultCSS = `/* Number box container */
.ugb-number-box {
\t
}

/* Number box item */
.ugb-number-box .ugb-number-box__item {
\t
}

/* Number box number */
.ugb-number-box .ugb-number-box__number {
\t
}

/* Number box content wrapper */
.ugb-number-box .ugb-number-box__content {
\t
}

/* Number box title */
.ugb-number-box .ugb-number-box__title {
\t
}

/* Number box description */
.ugb-number-box .ugb-number-box__description {
\t
}`

if ( isPro ) {
	addFilter( 'stackable.number-box.edit.inspector.after', 'stackable/number-box', ( output, design, props ) => {
		const { setAttributes } = props
		const {
			customCSS = '',
			customCSSUniqueID = '',
		} = props.attributes

		return (
			<Fragment>
				{ output }
				<PanelCustomCSSSettings
					value={ customCSS }
					defaultCSS={ defaultCSS }
					customCSSUniqueID={ customCSSUniqueID }
					blockProps={ props }
					mainClass={ mainClass }
					onChangeCustomCSS={ customCSS => setAttributes( { customCSS } ) }
					onChangeCompiledCSS={ customCSSCompiled => setAttributes( { customCSSCompiled } ) }
					onChangeCSSUniqueID={ customCSSUniqueID => setAttributes( { customCSSUniqueID } ) }
				/>
			</Fragment>
		)
	}, 11 )
}

const outputStyle = ( output, design, props ) => {
	const { customCSSCompiled = '' } = props.attributes
	if ( ! customCSSCompiled ) {
		return output
	}

	return (
		<Fragment>
			{ output }
			<style>{ customCSSCompiled }</style>
		</Fragment>
	)
}

addFilter( 'stackable.number-box.edit.output.before', 'stackable/number-box', outputStyle )
addFilter( 'stackable.number-box.save.output.before', 'stackable/number-box', outputStyle )

addFilter( 'stackable.number-box.mainclasses', 'stackable/number-box', ( classes, design, props ) => {
	const { customCSSUniqueID = '' } = props.attributes
	return {
		...classes,
		[ customCSSUniqueID ]: customCSSUniqueID,
	}
} )
