import './custom-css'
import { __ } from '@wordpress/i18n'
import { addFilter } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { isPro } from 'stackable'
import { PanelButtonSettings } from '@stackable/components'
import { RangeControl } from '@wordpress/components'

if ( isPro ) {
	addFilter( 'stackable.button.edit.inspector.general', 'stackable/button', ( ret, props ) => {
		const { setAttributes, attributes } = props
		const { buttons } = attributes
		return (
			<RangeControl
				label={ __( 'Number of Buttons' ) }
				value={ buttons }
				onChange={ buttons => setAttributes( { buttons } ) }
				min={ 1 }
				max={ 3 }
			/>
		)
	} )

	addFilter( 'stackable.button.edit.inspector.after', 'stackable/button', ( ret, design, props ) => {
		const { setAttributes, attributes } = props
		const {
			design2 = 'basic',
			color2,
			textColor2,
			size2,
			icon2,
			design3 = 'basic',
			color3,
			textColor3,
			size3,
			icon3,
			buttons = 1,
		} = attributes
		return (
			<Fragment>
				{ buttons > 1 &&
				<PanelButtonSettings
					className="ugb-has-designs"
					title={ __( 'Button #2 Settings' ) }
					initialOpen={ false }
					buttonColor={ color2 }
					buttonTextColor={ textColor2 }
					buttonSize={ size2 }
					buttonDesign={ design2 }
					buttonIcon={ icon2 }
					onChangeButtonColor={ color2 => setAttributes( { color2 } ) }
					onChangeButtonTextColor={ textColor2 => setAttributes( { textColor2 } ) }
					onChangeButtonSize={ size2 => setAttributes( { size2 } ) }
					onChangeButtonDesign={ design2 => setAttributes( { design2 } ) }
					onChangeButtonIcon={ icon2 => setAttributes( { icon2 } ) }
				/>
				}
				{ buttons > 2 &&
				<PanelButtonSettings
					title={ __( 'Button #3 Settings' ) }
					initialOpen={ false }
					buttonColor={ color3 }
					buttonTextColor={ textColor3 }
					buttonSize={ size3 }
					buttonDesign={ design3 }
					buttonIcon={ icon3 }
					onChangeButtonColor={ color3 => setAttributes( { color3 } ) }
					onChangeButtonTextColor={ textColor3 => setAttributes( { textColor3 } ) }
					onChangeButtonSize={ size3 => setAttributes( { size3 } ) }
					onChangeButtonDesign={ design3 => setAttributes( { design3 } ) }
					onChangeButtonIcon={ icon3 => setAttributes( { icon3 } ) }
				/>
				}
				{ ret }
			</Fragment>
		)
	} )
}
